#portal {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

.modal {
  @media (min-width: 700px) {
    width: 50%;
    left: unset;
    right: 5%;
    transform: translate(0);
  }
  @media (min-width: 1280px) {
    width: 30%;
  }
  color: white;
  position: fixed;
  top: 120px;
  left: 50%;
  transform: translate(-50%);
  z-index: 1000;
  width: 100%;
  height: 80vh;
  overflow-y: scroll;
  overflow: -moz-scrollbars-none;
}

.modal::-webkit-scrollbar {
  display: none;
}

.modal {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.modal-overlay {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  position: fixed;
  top: 100px;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.checkout-overlay {
  top: 0;
}

.checkout-modal {
  @media (min-width: 700px) {
    width: 90%;
    padding: 40px;
    height: 50%;
  }
  @media (min-width: 1200px) {
    width: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 70%;
  }
}
