.header {
  position: relative;
  background-color: $Primary-Dark;
  display: flex;
  justify-content: space-between;
  padding-left: 10%;
  padding-right: 10%;
  align-items: center;
  height: 100px;
  z-index: 2;
}
.header::after {
  content: '';
  position: absolute;
  bottom: -1%;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 1px;
  background-color: #fff;
}

.blure {
  position: fixed;
  transform: translateX(-150%);
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  transition: transform 0.5s linear, opacity 0.5s 0.2s linear;
  &.blure--active {
    transform: translateX(0);
    opacity: 1;
  }
}

a {
  text-decoration: none;
}

.cart-icon__container {
  position: relative;
  cursor: pointer;
  & div {
    position: absolute;
    right: -10px;
    top: -10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: $Primary-Orange;
    color: white;
    & span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.mobile-nav {
  position: absolute;
  transform: translateX(-150%);
  opacity: 0;
  width: 100%;
  border-radius: 5px;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: $White;
  z-index: 99;
  transition: transform 0.5s linear, opacity 0.5s 0.2s linear;
  & ul {
    list-style-type: none;
    @media (min-width: 600px) {
      display: flex;
      justify-content: center;
    }
  }
  &__title {
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translateX(-50%);
    @media (min-width: 600px) {
      top: 50%;
    }
  }
  &__link-container {
    position: relative;
    width: 90%;
    height: 217px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;
    background-color: $Primary-Gray;
    border-radius: 5px;
    @media (min-width: 600px) {
      width: 30%;
      height: 190px;
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 0;
    }
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
    &:nth-last-child(2) {
      @media (min-width: 600px) {
        margin-left: 10px;
        margin-right: 10px;
      }
    }
    & img {
      position: absolute;
      top: -20%;
      left: 50%;
      transform: translateX(-50%);
      width: 40%;
      @media (min-width: 400px) {
        width: 30%;
      }
      @media (min-width: 600px) {
        width: 60%;
      }
      @media (min-width: 1000px) {
        width: 40%;
      }
    }
  }
}

.mobile-nav.mobile-nav--active {
  transform: translateX(0);
  opacity: 1;
}

.mobile-nav__hamburger-icon-container {
  @media (min-width: 1280px) {
    display: none;
  }
}

.mobile-nav {
  @media (min-width: 1280px) {
    display: none;
  }
}

.desktop-nav {
  display: none;
  & a {
    color: white;
    text-transform: uppercase;
    &:hover {
      @media (min-width: 1280px) {
        color: $Primary-Orange;
      }
    }
  }
  &__link {
    margin-right: 20px;
  }
  @media (min-width: 1280px) {
    display: flex;
  }
  &__list {
    list-style-type: none;
    @media (min-width: 1280px) {
      display: flex;
    }
  }
}
